
// —ç—Ç–æ –∫–∞–∫-–±—ã —Å—Ç–µ–π—Ç —Å –¥–∞–Ω–Ω—ã–º–∏, –∫–æ—Ç–æ—Ä—ã–µ –º–æ–∂–Ω–æ –∏–∑–º–µ–Ω—è—Ç—å —á–µ—Ä–µ–∑ redux
let cartContent = { cartProduct: [], }

// —Ñ—É–Ω–∫—Ü–∏—è –∫–æ—Ç–æ—Ä–∞—è –∏–∑–º–µ–Ω—è–µ—Ç –Ω–∞—à redux-—Å—Ç–µ–π—Ç –ø–æ –Ω–∞–ø–∏—Å–∞–Ω–Ω–æ–π –ª–æ–≥–∏–∫–µ. –î–∞–Ω–Ω—ã–µ –±–µ—Ä—É—Ç—Å—è –∏–∑ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ action
export let cartReducer = (state = cartContent, action) => {
   switch (action.type) {
      case "ADD_CART":
         return { ...state, cartProduct: [...state.cartProduct, { ...action.payload }] }
      case 'DELETE_PRODUCT':
         return { ...state, cartProduct: [...action.payload] }
      case 'CLEAR_CART':
         return { ...state, cartProduct: [] }
      case 'UPDATE_CART':
         return { ...state, cartProduct: [...action.payload] }
      default:
         return state
   }
}