import React, { useState, useEffect } from 'react';
import PizzaItemControl from './PizzaItemControl.jsx'
import MyButton from './UI/MyButton/MyButton.jsx';
import { useDispatch, useSelector } from 'react-redux';

let PizzaItem = ({ pizza }) => {

   let dispatch = useDispatch()
   let cartItems = useSelector((state) => state.cartReducer.cartProduct)

   let [buttonActive, setButtonActive] = useState(false)

   // –æ—Ç–æ–±—Ä–∞–∂–∞–µ—Ç –∑–∞–∫–∞–∑–∞–Ω–Ω—É—é —Ç–µ–∫—É—â—É—é –ø–∏—Ü—Ü—É —Å —É–∫–∞–∑–∞–Ω–Ω—ã–º–∏ —Ä–∞–∑–º–µ—Ä–æ–º–∏
   let [pizzaAdded, setPizzaAdded] = useState({
      25: false, 30: false, 35: false
   })

   // —Å—Ç–µ–π—Ç —Å –≤—ã–±—Ä–∞–Ω–Ω—ã–º–∏ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º–∏ –ø–∏—Ü—Ü—ã
   let [pizzaParametr, pizzaParametrHandler] = useState([
      { dough: '—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ' },
      { size: '30 —Å–º.' },
      { price: pizza.price.average }
   ])

   // –∫–ª–∏–∫ –Ω–∞ —Ä–∞–∑–º–µ—Ä –∏–ª–∏ —Ç–∏–ø —Ç–µ—Å—Ç–∞, –ø–æ—Å–ª–µ –º–µ–Ω—è–µ—Ç—Å—è —Å—Ç–µ–π—Ç pizzaParametr –∏ –ø–µ—Ä–µ–¥–∞–µ—Ç –≤ –ø—Ä–æ–ø—Å
   let pizzaChangeDough = (e, size) => {

      // –µ—Å–ª–∏ –∫–ª–∏–∫ –ø–æ —É–∂–µ –≤—ã–±—Ä–∞–Ω–Ω–æ–º—É –ø—É–Ω–∫—Ç—É, —Ç–æ return –∏ —Å—Ç–µ–π—Ç –Ω–µ –º–µ–Ω—è–µ—Ç—Å—è
      if (e.target.classList.contains('_active')) { return }

      // –µ—Å–ª–∏ –∫–ª–∏–∫ –ø–æ —Å–≤–æ–±–æ–¥–Ω–æ–º—É —ç–ª–µ–º–µ–Ω—Ç—É, —Ç–æ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç—Å—è –∏–º—è –∫–ª–∞—Å—Å–∞ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ —á–µ—Ä–µ–∑ –∫–æ–ª–±–µ–∫
      if (e.target.classList.contains('pizza-item__type')) {
         let mass = [...pizzaParametr];
         mass[0].dough = e.target.textContent.trim();
         pizzaParametrHandler([...mass]);

      } else if (e.target.classList.contains('pizza-item__size')) {
         let mass = pizzaParametr;
         mass[1].size = e.target.textContent.trim();
         // –ø—Ä–∏ –≤—ã–±–æ—Ä–µ —Ä–∞–∑–º–µ—Ä–∞, –∫–æ–ª–±–µ–∫ –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–æ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç —Ä–∞–∑–º–µ—Ä –ø–∏—Ü—Ü—ã,–∏–∑ –Ω–µ–≥–æ –º–æ–∂–Ω–æ —É–∑–Ω–∞—Ç—å —Ü–µ–Ω—É
         mass[2].price = pizza.price[size];
         pizzaParametrHandler([...mass]);
      }

      // –µ—Å–ª–∏ –ø–∏—Ü—Ü–∞ —Å —Ç–∞–∫–∏–º —Ä–∞–∑–º–µ—Ä–æ–º —É–∂–µ –¥–æ–±–∞–≤–ª–µ–Ω–∞ –≤ –∫–æ—Ä–∏–∑–Ω—É, —Ç–æ –∑–∞–ø—Ä–µ—â–∞–µ—Ç –µ–µ –¥–æ–±–∞–≤–ª—è—Ç—å –µ—â–µ —Ä–∞–∑
      pizzaAdded[pizzaParametr[1].size.substr(0, 2)] ? setButtonActive(true) : setButtonActive(false)
   }

   let addCart = () => {
      // –≤ —Å—Ç–µ–π—Ç–µ –∏–∑–º–µ–Ω—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–µ–µ –¥–ª—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Ä–∞–∑–º–µ—Ä–∞ –∑–∞–∫–∞–∑–∞, —á—Ç–æ–±—ã –∑–∞–ø—Ä–µ—Ç–∏—Ç—å –ø–æ–≤—Ç–æ—Ä–Ω—ã–π –∫–ª–∏–∫
      let productSize = pizzaParametr[1].size.substr(0, 2)
      let fakeObj = { ...pizzaAdded }
      // –Ω–∞ —Ä–∞–∑–º–µ—Ä–µ –ø–∏—Ü—Ü—ã —Å—Ç–∞–≤–∏–º —Ñ–ª–∞–≥ true
      fakeObj[productSize] = true
      setPizzaAdded({ ...fakeObj })
      if (fakeObj[productSize] === true) { setButtonActive(true) }

      // —Å–æ–∑–¥–∞–µ—Ç –æ–±—ä–µ–∫—Ç —Å –¥–∞–Ω–Ω—ã–º–∏ –≤—ã–±—Ä–∞–Ω–Ω–æ–π –ø–∏—Ü—Ü—ã, –∏ –¥–æ–±–∞–≤–ª—è–µ—Ç –≤ –≥–ª–æ–±–∞–ª—å–Ω—ã–π —Å—Ç–µ–π—Ç
      let pizzaCartInfo = {
         dough: pizzaParametr[0].dough,
         size: pizzaParametr[1].size,
         price: pizzaParametr[2].price,
         img: './images/' + pizza.img,
         name: pizza.name,
         numbPizzaCart: null,
         totalCount: 1,
         totalPrice: pizzaParametr[2].price,
      }

      // cartItemsHandler([...cartItems, { ...pizzaCartInfo }])
      dispatch({ type: "ADD_CART", payload: pizzaCartInfo })
   }

   // –ø—Ä–∏ —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –º—ã –ø—Ä–æ–≤–µ—Ä—è–µ–º –µ—Å—Ç—å –ª–∏ —É–∫–∞–∑–∞–Ω–Ω–∞—è –ø–∏—Ü—Ü–∞ –≤ —Å—Ç–µ–π—Ç–µ –∑–∞–∫–∞–∑–∞, –∏ –∫–∞–∫–∏–µ —Ä–∞–∑–º–µ—Ä—ã –∑–∞–∫–∞–∑–∞–Ω—ã
   useEffect(() => {
      let fakeObj = { ...pizzaAdded }

      for (let i of cartItems) {
         // –ø—Ä–æ–≤–µ—Ä—è–µ–º –µ—Å–ª–∏ —Ç–µ–∫—É—â–∏–π –ø—Ä–æ–¥—É–∫—Ç —Å–æ–≤–ø–∞–¥–∞–µ—Ç —Å –ø—Ä–æ–¥—É–∫—Ç–æ–º –∏–∑ —Å—Ç–µ–π—Ç–∞ –∑–∞–∫–∞–∑–∞ 
         if (i.name === pizza.name) {
            let productSize = i.size.substr(0, 2)
            // –Ω–∞ —Ä–∞–∑–º–µ—Ä–µ –ø–∏—Ü—Ü—ã —Å—Ç–∞–≤–∏–º —Ñ–ª–∞–≥ true –≤ —Å–æ—Å—Ç–æ—è–Ω–∏–∏ –∑–∞–∫–∞–∑–∞ pizzaAdded
            fakeObj[productSize] = true
         }
      }
      setPizzaAdded({ ...fakeObj })
      if (fakeObj[30] === true) { setButtonActive(true) }
   }, [cartItems])

   return (
      <div className="main-pizza__item pizza-item">
         <div className="pizza-item__picture">
            <img src={'./images/' + pizza.img} alt={'Pizza'} />
         </div>
         <div className="pizza-item__content">
            <p className="pizza-item__title">{pizza.name}</p>
            <PizzaItemControl
               pizza={pizza}
               pizzaParametr={pizzaParametr}
               pizzaChangeDough={pizzaChangeDough}
            />
            <div className="pizza-item__bottom">
               <p className="pizza-item__price">{pizzaParametr[2].price} <span>‚ÇΩ</span></p>
               <MyButton
                  onClick={() => addCart()}
                  clsName="pizza-item__button"
                  buttonActive={buttonActive}
               > –î–æ–±–∞–≤–∏—Ç—å </MyButton>
            </div>
         </div>
      </div>
   )
}

export default PizzaItem