import { createStore, combineReducers, applyMiddleware } from 'redux';
import { cartReducer } from '../store/CartReducer.js';
import { authoReducer } from '../store/AuthoReducer.js';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from 'redux-thunk';


let rootReducer = combineReducers({
   cartReducer, authoReducer
})

// –ø–æ—Å–ª–µ —Ç–æ–≥–æ –∫–∞–∫ reducer –º–µ–Ω—è–µ—Ç —á—Ç–æ-—Ç–æ –≤ —Å—Ç–µ–π—Ç–µ, –æ–Ω –ø–µ—Ä–µ–¥–∞–µ—Ç –µ–≥–æ –≤ —Å—Ç–æ—Ä
export let store = createStore(rootReducer, composeWithDevTools((applyMiddleware(thunk))))

// –ø–∞–ª–∏–Ω–¥—Ä–æ–º—ã—á –ø—Ä–∏–º–µ—Ä —Ä–µ—à–µ–Ω–∏—è –º–æ–µ–≥–æ
