import { useDispatch, useSelector } from 'react-redux';

let CartDelete = ({ currentProduct, fakeCartItems }) => {

   let cartItems = useSelector((state) => state.cartReducer.cartProduct)
   // console.log(cartItems)

   let dispatch = useDispatch()

   let deleteProduct = () => {
      dispatch({ type: 'DELETE_PRODUCT', payload: fakeCartItems.filter((prod) => prod !== fakeCartItems[currentProduct]) })
   }

   return (
      <div onClick={() => deleteProduct()} className="cart-item__delete"></div>
   )
}

export default CartDelete