let authoState = {
   authorization: false
}

// —Ñ—É–Ω–∫—Ü–∏—è –∫–æ—Ç–æ—Ä–∞—è –∏–∑–º–µ–Ω—è–µ—Ç –Ω–∞—à redux-—Å—Ç–µ–π—Ç –ø–æ –Ω–∞–ø–∏—Å–∞–Ω–Ω–æ–π –ª–æ–≥–∏–∫–µ. –î–∞–Ω–Ω—ã–µ –±–µ—Ä—É—Ç—Å—è –∏–∑ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ action
export let authoReducer = (state = authoState, action) => {
   switch (action.type) {
      case "setAutho":
         return { ...state, authorization: !state.authorization }
      default:
         return state
   }
}