
let MyButton = ({ children, clsName, active, buttonActive, ...props }) => {

   // –¥–ª—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ PizzaCategories. –ï—Å–ª–∏ –ø—Ä–æ–ø—Å –ø—Ä–∏—Ö–æ–¥–∏—Ç —Å –∫–ª–∞—Å—Å–æ–º _active, —Ç–æ –æ–Ω –≤–µ—à–∞–µ—Ç—Å—è –Ω–∞ —ç–ª–µ–º–µ–Ω—Ç
   let activeClass = () => {

      // active –ø–µ—Ä–µ–¥–∞–µ—Ç—Å—è –∏–∑ –∫–Ω–æ–ø–∫–∏ –≤ PizzaCategories, –µ—Å–ª–∏ –≤ –Ω–µ–º true, —Ç–æ –ø—Ä–∏–º–µ–Ω—è–µ—Ç—Å—è –∫–ª–∞—Å—Å
      if (active || buttonActive === true) {
         let newName = clsName + ' _active';
         return newName
      } else { return clsName }
   }

   return (
      <button className={activeClass()} {...props}> {children} </button>
   )
}

export default MyButton
