export let PizzaSortedHandler = (activeType, currentSelect, pizzaItems, sortedPizzaHandler) => {

   // —Ñ–∏–ª—å—Ç—Ä—É–µ–º –ø–∏—Ü—Ü—ã –ø–æ –ø–∞—Ä–∞–º–µ—Ç—Ä—É –∏–∑ –∫–Ω–æ–ø–∫–∏
   let mass = [...pizzaItems].filter((pizza) => {
      let namePizza = Object.keys(pizza)[0]
      if (pizza[namePizza].type.hasOwnProperty(activeType)) { return pizza }
   })

   // –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ —Å–µ–ª–µ–∫—Ç–∞, —Å–æ—Ä—Ç–∏—Ä—É–µ—Ç –ø–∏—Ü—Ü—ã
   if (currentSelect === '1') {
      sortedPizzaHandler([...mass])
   } else if (currentSelect === '2') {
      let fakePizza = [...mass].sort((a, b) => {
         return b[Object.keys(b)[0]].price.low - a[Object.keys(a)[0]].price.low
      })
      sortedPizzaHandler([...fakePizza])
   } else if (currentSelect === '3') {
      let fakePizza = [...mass].sort((a, b) => {
         return a[Object.keys(a)[0]].price.low - b[Object.keys(b)[0]].price.low
      })
      sortedPizzaHandler([...fakePizza])
   }
}