import React, { useState, useEffect, ReactFragment } from 'react';
import tamingselect from '../pages/MainPage/MainPageSelect.js';
import MyButton from './UI/MyButton/MyButton.jsx';
import { PizzaSortedHandler } from '../utils/PizzaSortedHandler.js';

let PizzaSorted = ({ pizzaItems, sortedPizza, sortedPizzaHandler }) => {

   let [currentSelect, setCurrentSelect] = useState('1')

   let [activeType, setActiveType] = useState({
      all: false,
      meat: false,
      cheese: false,
      vegan: false,
      sharp: false,
   })

   let sortedType = (currentType) => {
      //–ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –∫–Ω–æ–ø–∫—É –∫–∞—Ç–µ–≥–æ—Ä–∏–π. –æ–±–Ω–æ–≤–ª—è–µ—Ç —Å—Ç–µ–π—Ç, —Å–±—Ä–∞—Å—ã–≤–∞—è —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–Ω–æ–ø–æ–∫, –∏ –ø—Ä–∏—Å–≤–∞–∏–≤–∞—è –Ω—É–∂–Ω–æ–π true
      let pizzaActive = {
         all: false,
         meat: false,
         cheese: false,
         vegan: false,
         sharp: false,
      }

      // currentType –±–µ—Ä–µ—Ç—Å—è –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–º –∏–∑ –∫–ª–∏–∫–∞ –ø–æ –∫–Ω–æ–ø–∫–µ
      pizzaActive[currentType] = true
      setActiveType({ ...pizzaActive })

      PizzaSortedHandler(currentType, currentSelect, pizzaItems, sortedPizzaHandler)
   }

   let changeHandler = () => {
      // –æ—Ç–ª–∞–≤–ª–∏–≤–∞–µ—Ç –∏–∑–º–µ–Ω–µ–Ω–∏—è value –≤ select, –∏ –ø–µ—Ä–µ–¥–∞–µ—Ç –≤ —Å—Ç–µ–π—Ç
      let elem = document.getElementById('select_id');
      if (currentSelect !== elem.value) {
         setCurrentSelect(elem.value)

         for (let i of Object.keys(activeType)) {
            if (activeType[i] === true) { PizzaSortedHandler(i, elem.value, pizzaItems, sortedPizzaHandler) }
         }
      }
   }

   // –∑–∞–ø—É—Å–∫–∞–µ—Ç –≤ —Ä–∞–±–æ—Ç—É –±–∏–±–ª–∏–æ—Ç–µ–∫—É —Å select –ø—Ä–∏ —Ä–µ–Ω–¥–µ—Ä–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
   useEffect(() => {
      tamingselect()
      setActiveType({ ...activeType, all: true })
   }, [])

   return (
      <div className="main-pizza__navigation">
         <div className="main-pizza__categories">
            <MyButton
               onClick={() => sortedType('all')}
               clsName={"main-pizza__categories-button"}
               active={activeType.all}
            >
               –í—Å–µ
            </MyButton>

            <MyButton
               onClick={() => sortedType('meat')}
               clsName={"main-pizza__categories-button"}
               active={activeType.meat}
            >
               –ú—è—Å–Ω—ã–µ
            </MyButton>

            <MyButton
               onClick={() => sortedType('cheese')}
               clsName={"main-pizza__categories-button"}
               active={activeType.cheese}
            >
               –°—ã—Ä–Ω—ã–µ
            </MyButton>

            <MyButton
               onClick={() => sortedType('vegan')}
               clsName={"main-pizza__categories-button"}
               active={activeType.vegan}
            >
               –í–µ–≥–µ—Ç–∞—Ä–∏–∞–Ω—Å–∫–∏–µ
            </MyButton>

            <MyButton
               onClick={() => sortedType('sharp')}
               clsName={"main-pizza__categories-button"}
               active={activeType.sharp}
            >
               –û—Å—Ç—Ä—ã–µ
            </MyButton>
         </div>

         <div className="main-pizza__sort">
            <span className="main-pizza__sort-title">–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ:</span>
            <div onClick={() => changeHandler()} className="main-pizza__sort-select">
               <select id={'select_id'} name="select_sort" className="turnintodropdown">
                  <option value="1" >–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏</option>
                  <option value="2" >—Ü–µ–Ω–µ (–º–∞–∫—Å)</option>
                  <option value="3" >—Ü–µ–Ω–µ (–º–∏–Ω)</option>
               </select>
            </div>
         </div>
      </div>
   )
}


export default PizzaSorted