import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import SvgCart from '../assest/svg/SvgCart';
import { useSelector } from 'react-redux';


let Header = () => {

   let cartItems = useSelector((state) => state.cartReducer.cartProduct)

   let [totalCart, setTotalCart] = useState(0)

   useEffect(() => {
      setTotalCart(cartItems.reduce((count, item) => count + item.totalCount, 0))
   }, [cartItems])

   return (
      <div className="header">
         <button className="header__login">–í–æ–π—Ç–∏</button>
         <Link to="/React-Pizza/" className="header__logo">
            <img src='./images/pizza-peace.png' alt="pizza-logo" className="header__img" />
            <div className="header__logo-text">
               <p className="header__title">REACT PIZZA</p>
               <p className="header__subtitle">—Å–∞–º–∞—è –≤–∫—É—Å–Ω–∞—è –ø–∏—Ü—Ü–∞</p>
            </div>
         </Link>
         <Link to={'/React-Pizza/cart'} className="header__cart">
            {<SvgCart color={'#fff'} />}
            <p className="header__cart-number">{totalCart}</p>
         </Link>
      </div>
   )
}

export default Header